  text-decoration: none;
}

#pageDocs {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  display: flex;
}

#pageDocs > a {
  color: #000;
  background-size: cover;
  border: 1px solid #000;
  width: 90svw;
  max-width: 400px;
  height: 90svw;
  max-height: 400px;
  padding: 10px;
  display: block;
}

@media (prefers-color-scheme: dark) {
  #pageDocs > a {
    color: #fff;
    border: 1px solid #ccc;
  }
}

#pageDocs > a:hover {
  background-color: #7f7f7f80;
}

#pageEdit {
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
  width: 100svw;
  height: 100svh;
  position: fixed;
  overflow: hidden;
}

#pageEdit #gesture-area {
  width: 100svw;
  height: 100svh;
  position: absolute;
}

#pageEdit #gesture-area img {
  transform-origin: 0 0;
